          id: 4
        },
        {
          name: '箱子标签已上传',
          id: 5
        }
      ],
      timeValue: [],
      timeValue2: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '全部',
            onClick(picker) {
              const end = '1'
              const start = '1'
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      shopid: 0,
      checked: {},
      Height: {
        height: '100px',
        paddingTop: '30px',
        borderLeft: '1px solid #ccc'
      },
      EditorderImportDialogTableVisible: false,//订单编辑
      EditwuliuVisible:false,//物流修改
      EditactiveName: '1',
      tableHeightMainBtn: {},
      tableHeightMain: {},
      tableHeightMain1: {},
      tableHeightMain3: {},
      tableHeightMain2: {},
      tableHeight: `80vh`
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event)
    },
    gofbasend(){
      this.$router.push('/fbasend')
    },
    // 点击编辑订单
    clickEditOrder(data) {
      this.EditorderImportDialogTableVisible = true
      this.getOrderDetail(data.id)
    },
    //点击修改物流
    clickEditwuliu(data){
      this.EditwuliuVisible=true
    },
        //初始化数据
    initData(u_id = sessionStorage.getItem('selectUId')) {
      this.multipleSelection.length = 0
      this.loading = true
      this.postRequest(`v1/fbaDetail?u_id=${u_id}`).then(res => {
        if (res.data.code == '200') {
          this.tableData = res.data.data
          this.total = this.tableData.length
          this.loading = false
          for (var i = 0; i < this.tableData.length; i++) {
            this.$set(this.checked, 'main' + i, false)
            var obj = {}
            obj.height = 100 * this.tableData[i].fba_content.length + 'px'
            obj.paddingTop =
              (100 * this.tableData[i].fba_content.length - 15) * 0.5 + 'px'
            obj.borderLeft = '1px solid #ccc'
            var obj1 = {}
            obj1.height = 100 * this.tableData[i].fba_content.length + 'px'
            obj1.paddingTop =
              (100 * this.tableData[i].fba_content.length - 30) * 0.5 + 'px'
            obj1.borderLeft = '1px solid #ccc'
            var obj2 = {}
            obj2.height = 100 * this.tableData[i].fba_content.length + 'px'
            obj2.paddingTop =
              (100 * this.tableData[i].fba_content.length - 45) * 0.5 + 'px'
            obj2.borderLeft = '1px solid #ccc'
            var obj3 = {}
            obj3.height = 100 * this.tableData[i].fba_content.length + 'px'
            obj3.paddingTop =
              (100 * this.tableData[i].fba_content.length - 60) * 0.5 + 'px'
            obj3.borderLeft = '1px solid #ccc'
            var objBtn = {}
            objBtn.height = 100 * this.tableData[i].fba_content.length + 'px'
            objBtn.paddingTop =
              (100 * this.tableData[i].fba_content.length - 25) * 0.5 + 'px'
            objBtn.borderLeft = '1px solid #ccc'
            this.$set(this.tableHeightMainBtn, 'main' + i, objBtn)
            this.$set(this.tableHeightMain3, 'main' + i, obj3)
            this.$set(this.tableHeightMain2, 'main' + i, obj2)
            this.$set(this.tableHeightMain1, 'main' + i, obj1)
            this.$set(this.tableHeightMain, 'main' + i, obj)
          }
        }
      })
    },
    orderNumSelectChange(val, data) {
      if (val) {
        this.multipleSelection.push(data.id)
      } else {
        var index = this.multipleSelection.indexOf(data.id)
        this.multipleSelection.splice(index, 1)
      }
    },
    // 复制函数
    onError(e) {
      this.$message({
        message: '复制失败！',
        type: 'error'
      })
    },
    copyFun(e) {
      this.$message({
        message: '复制成功！',
        type: 'success'
      })
    },
    handleClickTab() {},
    //获得国家数据
    initCountryCity() {
      this.postRequest(`/v1/goodsCoutry`).then(res => {
        //  console.log(res.data.data);
        this.countryList = res.data.data
      })
    },
    // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if (res.data.code == 200) {
        this.storeList = res.data.data
        if (this.storeList.length > 0) {
          this.shopid = this.storeList[0].id
        }
      }
    },
    //店铺名称
    changeshop(val) {
      if (val == 0) {
        this.checkboxshop = [0]
      } else {
        if (this.checkboxshop.indexOf(0) != -1) {
          this.checkboxshop.splice(this.checkboxshop.indexOf(0), 1)
        }
      }
      if (this.checkboxshop.length == 0) {
        this.checkboxshop = [0]
      }

      this.fbaManagement.store_id = this.checkboxshop
      //  this.initData();
    },
    shopChange(val) {
      this.uploadData.shopid = val
    },
    //订单状态改变
    changeState(status) {
      this.fbaManagement.order_status = status
      this.initData() //
    },
    //入仓步骤状态改变
    changestep(status) {
      this.fbaManagement.step_status = status
      this.initData()
    },
    //排序方式改变
    changway(status) {
      this.fbaManagement.sort_way = status
    },
    //搜索
    searchorder() {
      this.fbaManagement.keyword = this.keyword
      this.fbaManagement.country_id = this.countryselect
      this.fbaManagement.times = this.timeValue
      this.fbaManagement.times2 = this.timeValue2
      // this.initData();
    },
    //重置
    resetorder() {
      this.ordernum = ''
      this.countryselect = []
      this.timeValue = []
      this.orderManagement.keyword = this.keyword
      this.orderManagement.country_id = this.countryselect
      this.orderManagement.times = this.timeValue
      // this.initData();
    },
    //全选改变
    checkedAllOrderChange(val) {
      if (val) {
        this.multipleSelection.length = 0
        for (var i = 0; i < this.tableData.length; i++) {
          this.$set(this.checked, 'main' + i, true)
          this.multipleSelection.push(this.tableData[i].id)
        }
      } else {
        for (var i = 0; i < this.tableData.length; i++) {
          this.$set(this.checked, 'main' + i, false)
          this.multipleSelection.length = 0
        }
      }
      //  console.log(this.multipleSelection);
    },
    handleSizeChange(val) {
      this.$store.commit('FBA_ORDER_LIMIT', val)
    },
    handleCurrentChange(val) {
      this.$store.commit('FBA_ORDER_PAGE', val)
    }
  },
